.Modal {
    position: fixed;
    z-index: 500;
    background-color: #18191a;
    width: 70%;
    -webkit-box-shadow: 1px 1px 1px rgb(61, 64, 67);
            box-shadow: 1px 1px 1px rgb(61, 64, 67);
    border: 1px solid rgb(44, 44, 48);
    border-radius: 20px;
    text-align: center;
    padding: 16px;
    left: 15%;
    top: 30%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -webkit-transition: all 0.3s ease-out;
    -o-transition: all 0.3s ease-out;
    transition: all 0.3s ease-out;
}

.Modal ul {
    display: inline-block;
    margin: 0;
    padding: 0;
}

.Modal li {
    margin-left: 5px;
    list-style: none;
    float: left;
    padding: 2px 5px;
}

@media (min-width: 600px) {
    .Modal {
        width: 500px;
        left: calc(50% - 250px);
    }
}


