.Toolbar{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #18191A;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    padding: 0 20px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    z-index: 90;
}

.Toolbar nav{
    height: 100%;
}

.Logo{
    height: 80%;
}

@media(max-width: 499px){
    .DesktopOnly{
        display: none;
    }
}

@media(min-width: 500px){
    .MobileOnly{
        display: none;
    }
}

.Menu{
    position: relative;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    /* background-color: white; */
    padding: 8px 13px;
    height: 44.8px;
    width: 60px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    border-radius: 8px;
    font-size: 0.8em;
    cursor: pointer;

}

.Burger{
    width: 34px;
    height: 6px;
    margin-top: 10px;
    background: white;
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 5px gray;
            box-shadow: 0 2px 5px gray;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.Burger::before,
.Burger::after{
    content: '';
    position: absolute;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    width: 34px;
    height: 5px;
    background: white;
    border-radius: 5px;
    -webkit-box-shadow: 0 2px 5px gray;
            box-shadow: 0 2px 5px gray;
    -webkit-transition: all .5s ease-in-out;
    -o-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}

.Burger::before{
    -webkit-transform: translateY(-10px);
        -ms-transform: translateY(-10px);
            transform: translateY(-10px);
}

.Burger::after{
    -webkit-transform: translateY(10px);
        -ms-transform: translateY(10px);
            transform: translateY(10px);
}

.Menu:active{
    color: white;
    background-color: #44a5ff;
}


