.BuildControls {
    width: 100%;
    background-color: #18191A;
    /* color: white; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: column;
        flex-flow: column;
    -ms-flex-align: center;
        align-items: center;
    -webkit-box-shadow: 0 2px 1px #ccc;
            box-shadow: 0 2px 1px #ccc;
    margin: auto;
    padding: 10px 0;
}

.Checkout {
    outline: none;
    cursor: pointer;
    border: 1px solid rgb(23, 23, 23);
    color: white;
    font-family: inherit;
    font-size: 1.2em;
    padding: 15px 30px;
    -webkit-box-shadow: 2px 2px 2px rgb(35, 35, 35);
            box-shadow: 2px 2px 2px rgb(35, 35, 35);
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    position: relative;
    background: #40a4c8;
    -webkit-transition-property: color;
    -o-transition-property: color;
    transition-property: color;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
       transition-duration: 0.3s;
}

.Checkout:before {
    content: "";
    position: absolute;
    color: #6c757d;
    z-index: -1;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgb(52, 52, 52);
    -webkit-transform: scaleX(1);
    -ms-transform: scaleX(1);
        transform: scaleX(1);
    -webkit-transform-origin: 50%;
    -ms-transform-origin: 50%;
        transform-origin: 50%;
    -webkit-transition-property: transform;
    -webkit-transition-property: -webkit-transform;
    transition-property: -webkit-transform;
    -o-transition-property: transform;
    transition-property: transform;
    transition-property: transform, -webkit-transform;
    -webkit-transition-duration: 0.3s;
    -o-transition-duration: 0.3s;
       transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    -o-transition-timing-function: ease-out;
       transition-timing-function: ease-out;
}

.Checkout:hover, .Checkout:active {
    color: white;
}

.Checkout:hover:before, .Checkout:active:before {
    -webkit-transform: scaleX(0);
    -ms-transform: scaleX(0);
        transform: scaleX(0);
}

.Checkout:disabled {
    -webkit-transform: none;
    -ms-transform: none;
        transform: none;
    background-color: #c7c6c6;
    cursor: not-allowed;
    border: 1px solid #ccc;
    color: #888888;
}

.Checkout:hover:disabled {
    -webkit-transform: none;
    -ms-transform: none;
        transform: none;
}

.Checkout:not(:disabled) {
    -webkit-animation: enable 0.3s linear;
            animation: enable 0.3s linear;
}

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


